import Common from '@/lib/common'
export default {
  data(){
    return{
      postName:"",
      postNumber:"",
      status:'',
      taxWrongText:"",
      paySuccessColor:"red",
      taxSuccessColor:"red",
      name:'',
      number:''
    }
  },
  mounted(){
    var that =this;
    Common.getPostNumber(function(res){
      if(res.data.rows[0].code=='200'){
        that.postName =res.data.rows[0].data.postName;
        that.name=':';
        that.number=':';
        that.postNumber =res.data.rows[0].data.postNumber;
        if(res.data.rows[0].data.status==='1'){
          that.taxWrongText = '';
          that.status=':';
        }

      }

    });
  },
  methods: {
    goback1() {
      this.$router.push("/index")
    },
    loading(that,data){
      let arr = [data];
      let str = arr.join('</br>');
      that.$message({
        dangerouslyUseHTMLString:true,
        message:str,
        type:'warning'
      })
    },
    getPost(){
      var that = this;
      Common.changPostStatus(function(res){
        if(res.data.rows[0].code=='200'){
          that.taxWrongText = ''
          that.loading(that,"");
        }else if(res.data.rows[0].code=='202'){
          that.loading(that,"");
        }else{
          that.loading(that,"");
        }
      });
    }
  }

}



