import Common from '@/lib/common'
export default {
  data () {
    return {
      radio: '1',
      name: '',
      phone:'',
      post:'',
      address:'',
      showPrise:true,
      showPriseBody:false,
      chooseMothed:1, // 1    2  
      clientKey:''
    };
  },
  created(){
    if(!window.localStorage){

      alert("localstorage");
      return false;
    }else{
      var storage=window.localStorage;
      this.clientKey= storage.getItem("clientKey");
      Common.clientKey=storage.getItem("clientKey");
    }
  },
  methods: {
    sourceFilter(){
      var that = this;
      that.showPrise=true;
      that.showPriseBody=false
      that.chooseMothed=1
    },
    sourceFilter1(){
      var that = this;
      that.showPrise=false;
      that.showPriseBody=true;
      that.chooseMothed=2
    },
    returnPage(){
      var that = this;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // alert(that.name);
        if(that.chooseMothed==1){
          Common.getByself(this);
        }else if(that.chooseMothed=2){
          if(that.name=='' || that.phone=='' || that.post=='' || that.address==''){
            this.$message({
              type: 'fail',
              message: ''
            });
            return;
          }else{
            Common.insertAddress(this);
          }
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}



