// image-conversion
import imageConversion from 'image-conversion'
export default {
    methods: {
        // ,,,4M
        beforeUpload (file) {
            return new Promise((resolve, reject) => {
                let isLt2M = file.size / 1024 / 1024 < 4 // 4MB
                if (isLt2M) {
                    resolve(file)
                }
                console.log(file)
                // 400KB,400,
                imageConversion.compressAccurately(file, 400).then(res => {
                    resolve(res)
                })
            })
        },
        //,4M,2000
        beforeUpload2 (file) {
            // 4m,2000
            return new Promise((resolve, reject) => {
                let _URL = window.URL || window.webkitURL
                let isLt2M = file.size / 1024 / 1024 > 4 // 4MB
                // 
                let img = new Image()
                img.onload = function () {
                    file.width = img.width // widthfile
                    file.height = img.height // heightfile
                    let valid = img.width > 2000 // 2000
                    // console.log(11, file)
                    // ,compressAccurately
                    if (valid || isLt2M) {
                        imageConversion.compressAccurately(file, {
                            size: 400,
                            width: 2000
                        }).then(res => {
                            // console.log(33, res)
                            resolve(res)
                        })
                    } else resolve(file)
                }
                // 
                img.src = _URL.createObjectURL(file)
            })
        },
    }
}




