import Common from "../../lib/common.js"
export default {
    data(){
        return {
          isTrue:false,

          userInfo:{headPic: require('@/assets/static/images/touxiang.jpg'),userName:''},
          text:[
            {
                text: '',
                url: 'Process',
                tip: '',
                icon:'el-icon-share',
            },
            {
              text: '',
              url: 'Deal',
              tip: '',
              icon:'el-icon-document',
            },
              {
              text: '',
              url: 'PostPage',
              tip: '',
              icon:'el-icon-zoom-in',
            },
            //     {
            //     text: '',
            //     url: 'payAgain',
            //     tip: ''
            //   },
            {
              text: '',
              url: 'Help',
              tip: '',
              icon:'el-icon-phone',
            },
              {
                  text: '',
                  url: 'uploadImg',
                  tip: '',
                  icon:'el-icon-phone',
              }
          ]
        }
    },
    methods:{
      clickRoute(){
        this.$emit('handerSelect',4);  //
      },
        loading(that,data){
            let arr = [data];
            let str = arr.join('</br>');
            that.$message({
                dangerouslyUseHTMLString:true,
                message:str,
                type:'warning'
            })
        },
        reload(){
          var that = this;
            Common.reload(that);
        },
        payAgain(){
          var that = this;
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                Common.payAgain(function(res){
                    if(res.data.rows[0].code=='200'){
                        that.loading(that,"");
                    }else if(res.data.rows[0].code=='201'){
                        that.loading(that,"");
                    }else{
                        that.loading(that,"");
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        }
    },
    mounted() {
      var that = this;
      Common.FindUser(function (res) {
        that.userInfo.userName = res.data;
        if(res.data===''|| res.data==='1'||res.data ===''
            ||res.data ===''||res.data ===''||res.data ===''){
            that.isTrue=true;
        }
      },function () {

      })
    }
}



