import Common from '@/lib/common'
export default {
  data(){
    return{
      dealStatus:"",
      taxStatus:"",
      kksjContent:'',
      payWrongText:"",
      taxWrongText:"",
      paySuccessColor:"red",
      taxSuccessColor:"red",
      payTime:'',
      userName:'',
      money:'0',
      yhs:'0',
      showRight:false,
      showWrong:true,
    }
  },
  mounted(){
    var that =this;
    Common.getPayStatus(function(res){

      if(res.data.code!="200"){
        that.dealStatus="";
        that.taxStatus="";
        that.payWrongText="";
        that.taxWrongText="";
      }else{
        if(res.data.context[0]==''){
          that.dealStatus="";
          that.taxStatus="";
          that.payWrongText="";
          that.taxWrongText="";
        }else{
          if( res.data.context[0].status=="0"){

            that.userName = res.data.userName;
            that.showRight=true;
            that.showWrong=false;
            that.dealStatus="";
            that.kksjContent=':'
            that.payTime=res.data.context[0].xtLrsj;
            that.money = res.data.context[2].jehjxx;
            that.yhs = res.data.context[2].yhs;
            that.paySuccessColor="green"
            that.payWrongText="";
          }
          if( res.data.context[0].status=="2"){
            that.dealStatus="";
            that.paySuccessColor="green"
            that.payWrongText="";
          }
          if( res.data.context[0].status=="3"){
            that.dealStatus="";
            that.paySuccessColor="green"
            that.payWrongText="";
          }
        }
        if(res.data.context[1]==''){
          that.taxStatus="";
          that.taxWrongText="";
        }else{
          if(res.data.context[1].procResult=="000000"){
            that.taxStatus="";
            that.taxWrongText="";
            that.taxSuccessColor="green";
          }else{
            that.taxStatus="";
            that.taxWrongText=''+res.data.context[1].procResult + ',' +res.data.context[1].errTex;
            that.taxSuccessColor="red";
          }
        }
      }
    });
  },
  methods: {
    goback1() {
      this.$router.push("/index")
    },
    loadAlert(){
      Common.loading(this,'');
    }
  }

}



