  import Common from '@/lib/common'

export default {
  props:{
    spzt:{
      type:String,
      default:null
    },
    cards:{
      type:Array,
      default:null
    }
  },
    data(){
        return{
            backhidden:true,
            spwz:"",
            spwzsecend:"3-5",//
            spljwz:""//
        }
    },
  mounted(){//
    switch (this.spzt) {
      case 1:
      case "1":
        this.spwz=""
        this.spwzsecend="3-5"
        break;
      case 2:
      case "2":
        this.spwz=""
        this.spwzsecend=""
        break;
      case 3:
      case "3":
          this.spwz=""
          this.spwzsecend=""
        // this.$emit('goOtherPage');
        break;
      case 4:
      case "4":
        this.spwz=""
        this.spwzsecend=""
        break;
      case 6:
      case "6":
        this.spwz=""
        this.spwzsecend="..."
        this.spljwz=""
        break;
      case 7:
      case "7":

        this.spwz=""
        this.spwzsecend=""
        break;
      case "8":
        this.spwz=""
        this.spwzsecend=""
        break;

      case 9:
      case "9":
        this.spwz=""
        this.spwzsecend="3-5"
        break;
    }
  },
    methods:{
        returnindex(){
            var that = this;
            //spzt
            if(this.spzt=="7"){
              this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
                Common.postGet(that);
                this.$router.push("/index")
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: ''
                });
                  this.$router.push("/index")
              });
            } else if (this.spzt == "1" || this.spzt == "2" || this.spzt == "8" || this.spzt == "9"){
              this.$router.push("index")
            }else if(this.spzt=="5"){
              this.spljwz = ""
              this.$router.push("/YoujiPages/HandlePost")
            }
            else{
              this.$router.push("index")
            }
            
        },
    }
}



